import { html, css, LitElement, property } from 'lit-element'
import { NodeRegistry } from './registry'
import {styles} from './styles'

// We use drag and drop with mouse EventSource
// https://javascript.info/mouse-drag-and-drop

const NODE_SPACING = 10;

export interface DropEvent {
    detail:{
      type:string,
      x: number,
      y: number
    }
};

export class PalletteElement extends LitElement {

  registry: NodeRegistry;
  droppable;

  @property({type: String})
  dropSelector;

  static get styles() {
    return [
      styles, css`
      .pallette {
        position: absolute;
        top: 0px;
        bottom: 0px;
        left: 0px;
        background: #f3f3f3;
        width: 180px;
        text-align: center;
        user-select: none;
        border: 1px solid #bbb;
        box-sizing: border-box;
        transition: width 0.2s ease-in-out;
      }
      .node {
        cursor: move;
        background: #fff;
        margin: ${NODE_SPACING}px auto;
        height: 25px;
        border-radius: 5px;
        border: 1px solid #999;
        background-position: 5% 50%;
        background-repeat: no-repeat;
        width: 120px;
        background-size: contain;
        position: relative;
      }
      .label {
        color: #333;
        font-size: 13px;
        margin: 4px 0 4px 32px;
        line-height: 20px;
        overflow: hidden;
        text-align: center;
        user-select: none;
      }
      .icon-container {
        position: absolute;
        text-align: center;
        top: 0;
        bottom: 0;
        left: 0;
        width: 30px;
        border-right: 1px solid rgba(0,0,0,0.05);
        background-color: rgba(0,0,0,0.05);
      }
      .icon {
        display: inline-block;
        width: 20px;
        height: 100%;
        background-position: 50% 50%;
        background-size: contain;
        background-repeat: no-repeat;
      }
      .port {
        position: absolute;
        top: 8px;
        left: -5px;
        box-sizing: border-box;
        background: #FFFFFF;
        border-radius: 3px;
        width: 10px;
        height: 10px;
        border: 1px solid #999;
      }
      .port-output {
        left: auto;
        right: -6px;
      }
    `]
  }

  onMouseDown = (e) => {
    this.droppable = null
    let node = e.currentTarget

    // where we clicked in the node
    const shiftX = e.clientX - node.getBoundingClientRect().left;
    const shiftY = e.clientY - node.getBoundingClientRect().top;

    // make a copy to drag around
    node = node.cloneNode(true)
    this.shadowRoot.append(node)
  
    node.style.position = 'absolute'
    node.style.zIndex = 1000

    // drags the node where we clicked on it
    function moveAt(pageX, pageY) {
      node.style.left = pageX - shiftX + 'px';
      node.style.top = pageY - shiftY - NODE_SPACING + 'px';  // 10 for margin
    }

    // move the node under the pointer
    moveAt(e.pageX, e.pageY);

    let currentDroppable = null

    const onMouseMove = (event) => {
      moveAt(event.pageX, event.pageY);

      node.hidden = true;
      let elemBelow = document.elementFromPoint(event.clientX, event.clientY);
      node.hidden = false;

      // mousemove events may trigger out of the window (when the node is dragged off-screen)
      // if clientX/clientY are out of the window, then elementFromPoint returns null
      if (!elemBelow) return;

      // we always drop into a flow-element that is below us
      const droppableBelow = elemBelow.closest(this.dropSelector);
      if (currentDroppable != droppableBelow) {
        // we're flying in or out...
        // note: both values can be null
        //   currentDroppable=null if we were not over a droppable before this event (e.g over an empty space)
        //   droppableBelow=null if we're not over a droppable now, during this event
    
        if (currentDroppable) {
          // the logic to process "flying out" of the droppable (remove highlight)
          console.log('leave drop selector')
        }
        currentDroppable = droppableBelow;
        if (currentDroppable) {
          // the logic to process "flying in" of the droppable
          console.log('enter drop selector')
          // save for mouseup
          this.droppable = currentDroppable
        }
      }
    }

    // (2) move the node on mousemove
    document.addEventListener('mousemove', onMouseMove);

    // (3) drop the node, remove unneeded handlers
    // call the component to handle the new element
    node.onmouseup = (e) => {
      document.removeEventListener('mousemove', onMouseMove);
      node.onmouseup = null;
      // remove the copy we used for drawing
      node.parentNode.removeChild(node);
      if (this.droppable) {
        const left = parseInt(node.style.left, 10)
        const top = parseInt(node.style.top, 10) + NODE_SPACING
        const type = node.dataset.type
        this.dispatchEvent(new CustomEvent('dropNode', {
          detail:{
            type,
            x: left,
            y: top
          }
        }))
        this.droppable = null
      }
    };
  }

  render() {
    const types = this.registry.getTypes()
    return html`<div class="pallette">
      ${types.map(type => html`
        <div class="node ui-draggable ui-draggable-handle" @mousedown=${this.onMouseDown}
          data-type="${type.type}" style="background-color: ${type.color}; height: 28px;">
          <div class="label" dir="">${type.type}</div>
          <div class="icon-container">
            <div class="icon" style="background-image: url(&quot;icons/${type.icon}&quot;);"></div>
          </div>
          ${type.outputs ? html`<div class="port port-output" style="top: 9px;"></div>`:''}
          ${type.inputs ? html`<div class="port" style="top: 9px;"></div>`:''}
        </div>`)}
      </div>`
  }
}

