import { Flow } from './flow'
import { html, render } from 'lit-html'
import { FlowElement, NodeEvent } from './flow-element'
import { PalletteElement, DropEvent } from './pallette-element'
import { NodeType, NodeRegistry } from './registry'
import { ConfigElement } from './config-element'

window.customElements.define('flow-element', FlowElement)
window.customElements.define('pallette-element', PalletteElement)
window.customElements.define('config-element', ConfigElement)

// get flow element for relaying event
let flowElement = null
window.addEventListener('DOMContentLoaded', () => {
  flowElement = document.querySelector('flow-element') as FlowElement
});

// test fixtures from the servers
const testTypes:NodeType[] = [
  {
    type:'inject',
    icon:'inject.png',
    color:'lightgray',
    defaults: {},
    inputs: 0,
    outputs: 1
  },
  {
    type:'debug',
    icon:'debug.png',
    color:'lightgreen',
    defaults: {},
    inputs: 1,
    outputs: 0
  },
  {
    type:'splitter',
    icon:'split.png',
    color:'beige',
    defaults: {},
    inputs: 1,
    outputs: 4
  }
]

const testFlow:Flow = {
  tabs:[{
    name: 'test tab',
    nodes:[      {
      id: 'a',
      type: 'splitter',
      name: 'first node',
      x: 100,
      y: 100,
      wires: [['b','c']]
    },
    {
      id: 'b',
      type: 'debug',
      name: 'second debug',
      x: 250,
      y: 150,
      wires: []
    },
    {
      id: 'c',
      type: 'splitter',
      name: 'second splitter',
      x: 100,
      y: 300,
      wires: []
    },
  ]
  }]
}

const registry = new NodeRegistry()
testTypes.forEach(type => registry.register(type))

render(
  html`
    <pallette-element .registry=${registry} dropSelector="flow-element"
      @dropNode=${(e: DropEvent) => flowElement.dropNode(e.detail.type, e.detail.x, e.detail.y)}></pallette-element>
    <flow-element .flow=${testFlow} .registry=${registry}
      @configNode=${(e: NodeEvent) => console.log(`config ${e.detail.id}`)}
      @selectNode=${e => console.log(`select ${e.detail.id}`)}></flow-element>
    <config-element .registry=${registry}></config-element>
    `,
  document.querySelector('#demo'));