
export interface NodeType {
  type:string
  icon:string
  defaults: any
  color: string
  inputs: number
  outputs: number
}

export class NodeRegistry {

  types: Map<string, NodeType>

  constructor() {
    this.types = new Map<string, NodeType>()
  }

  register(nodeType: NodeType) {
    if (this.types[nodeType.type]) {
      throw new Error('type already registered')
    }
    this.types.set(nodeType.type, nodeType);
  }

  getNode(type: string):NodeType {
    return this.types.get(type);
  }

  getTypes() {
    return Array.from<NodeType>(this.types.values())
  }
}

