import { EventEmitter } from 'events';

import { NodeRegistry } from './registry';
import d3 from './d3';
import { Selection } from 'd3-selection';

// flow is an array of nodes
export interface Node {
  name: string,
  type: string,
  id: string,
  x: number,
  y: number,
  wires: [string[]?]
};

interface Wire {
  fromNode:string,
  fromPort: number,
  toNode:string
}

export class FlowEditor extends EventEmitter {

  private registry: NodeRegistry;
  private flow: Node[];           // array of nodes and wires
  private flowWires: Wire[];      // wires list derived from flow on update

  private svg;       // passed in svg
  private wires;     // wires group    
  private nodes;     // nodes group

  private dragLine;               // line for dragging

  private mouseDownNode: {        // node and port during connector drag
    node: Node,
    port: number,
    coords: { x:number, y:number }
  };  

  private selectedLink: string;   // id of currently selected link
  private selectedNode: string;   // currently selected node

  constructor(registry: NodeRegistry, svg: Selection<SVGSVGElement, unknown, null, undefined>) {
    super();
    this.registry = registry;
    this.svg = svg;
  }

  /**
   * Initialize the editor with the flow to edit
   *
   * @param flow
   */
  init(flow) {
    this.flow = flow;

    // handle dragging line, resetting selection, deleting lines and nodes
    this.svg
      .on('mousemove', (event: any) => {
        if (!this.mouseDownNode) return;
        let sourceX = this.mouseDownNode.coords.x;
        let sourceY = this.mouseDownNode.coords.y;
        let targetX = d3.pointer(event)[0];
        let targetY = d3.pointer(event)[1];
        this.dragLine.attr('d', `M ${sourceX} ${sourceY} C ${sourceX+50} ${sourceY}, ${targetX-50} ${targetY}, ${targetX},${targetY}`);
      })
      .on('mouseup', () => {
        if (this.mouseDownNode) {
          this.mouseDownNode = null;
          this.dragLine
            .classed('hidden',true)
            .attr('d', 'M0,0L0,0');
        }
      })
      .on('click', () => {
        this.resetSelection();
      })
      .on('keydown', (event: any) => {
        event.stopPropagation();
        if (event.key === 'Delete' || event.key === 'Backspace') {
          if (this.selectedNode) {
            let nodeId = this.selectedNode.replace('node-','');
  
            // remove all the wires to the node, and the node
            this.flow = this.flow.reduce((result, node) => {
                if (node.id !== nodeId) {
                  for (let i in node.wires) {
                    node.wires[i] = node.wires[i].filter((id) => id !== nodeId);
                  }
                  result.push(node)
                }
                return result;
              },[]);
            this.selectedNode = null;
            this.updateFlow();
          }
          if (this.selectedLink) {
            let [prefix, fromNode, port, toNode ] = this.selectedLink.split('-');
  
            // remove that wire from the node
            for (let node of this.flow) {
              if (node.id === fromNode) {
                let portNum = parseInt(port);
                node.wires[portNum] = node.wires[portNum].filter( nodeId => nodeId !== toNode);
              }
            }
            this.selectedLink = null;
            this.updateFlow();
          }
        }
      })
      .on("focus", () => {});   // this is needed to get keydown events

    // so we can resize the whole flow (future)
    const svgGroup = this.svg.append('g').classed('zoom-group', true);

    // wires and nodes layer groups
    this.wires = svgGroup.append('g').classed('all-wires', true);
    this.nodes = svgGroup.append('g').classed('all-nodes', true);

    // line displayed when dragging new links
    this.dragLine = svgGroup.append('path')
      .attr('class', 'hidden flow-link')
      .attr('d', 'M0,0L0,0');

  }

  private resetSelection() {
    if (this.selectedLink) {
      this.wires.select(`#${this.selectedLink}`)
        .classed('selected', false)
      this.selectedLink = null;
    }
    if (this.selectedNode) {
      this.nodes.select(`#${this.selectedNode}`)
        .classed('selected', false)
      this.selectedNode = null;
    }
  }

  private updateWires() {
    // generate flowWires array to bind to wires
    this.flowWires = [];
    for(let flowNode of this.flow) {
      for (let i=0; i < flowNode.wires.length; i++) {
        for (let j in flowNode.wires[i]) {
          this.flowWires.push({
            fromNode: flowNode.id,
            fromPort: i,
            toNode: flowNode.wires[i][j]
          });
        }
      }
    }
  }

  private generatePath(wirePath) {
    const sourceNode = this.nodes.select(`#node-${wirePath.fromNode}`);
    let sourceX = sourceNode.node().getBBox().width;
    sourceX = sourceNode.datum().x + sourceX;
    let sourceY = sourceNode.datum().y + 15 + wirePath.fromPort * 15;
    
    const targetNode = this.nodes.select(`#node-${wirePath.toNode}`);
    // all input nodes are in the same place
    let targetX = targetNode.datum().x + 5;
    let targetY = targetNode.datum().y + 15;
    return `M ${sourceX} ${sourceY} C ${sourceX+50} ${sourceY}, ${targetX-50} ${targetY}, ${targetX},${targetY}`;
  }

  public updateFlow() {

    const thisEditor = this;

    const wireId = (d) => `wire-${d.fromNode}-${d.fromPort}-${d.toNode}`;

    this.updateWires();

    // draw nodes
    let nodes = thisEditor.nodes.selectAll('g.node');

    // bind node data to the selected nodes.  identify unique node by id.
    nodes = nodes.data(thisEditor.flow, function (d) {
      return d.id;
    });

    // draw any new nodes, selected using enter() call
    let newNodes = nodes
      .enter()
      .append('g')
      .classed('node', true)
      .attr('transform', function (d) {
        return `translate(${d.x},${d.y})`;
      })
      .attr('id', (d) => `node-${d.id}`)
      .style('fill', function (d) {
        return thisEditor.registry.getNode(d.type).color;
      });
    
    // draw the rectangle, then save it for width adjustment
    const nodeRect = newNodes
      .append('rect')
      .attr('width', 10)
      .attr('height', (d) => {
        const wires = d.wires?.length || 1;
        return 15 + wires * 15;
      })
      .attr('x', 5) // for possible input connector
      .attr('rx', 5)
      .attr('ry', 5);

    // draw the label in the node
    const text = newNodes
      .append('text')
      .attr('x', 40)
      .text((d) => d.name)
      .attr('y', function (d) {
        return this.parentNode.getBBox().height / 2 - 3;
      })
      .classed('label', true);

    // adjust the rect width to contain the text
    nodeRect.attr('width', function (d) {
      return this.parentNode.getBBox().width + 20;
    });

    // draw an input connector if the node has one
    newNodes
      .filter((d) => thisEditor.registry.getNode(d.type).inputs)
      .append('rect')
      .attr('width', 10)
      .attr('height', 10)
      .attr('x', 0)
      .attr('y', 10)
      .attr('rx', 2)
      .attr('ry', 2)
      .classed('connector', true);

    // draw all of the output connectors
    newNodes.each(function (d) {
      const node = d3.select(this);

      // x location on the right
      let x = node.node().getBBox().width;

      // hack: adjust for when we've added an input node
      x = thisEditor.registry.getNode(d.type).inputs ? x - 5 : x;

      for (let i = 0; i < d.wires?.length; i++) {
        let y = 10 + i * 15;
        node
          .append('rect')
          .attr('width', 10)
          .attr('height', 10)
          .attr('x', x)
          .attr('y', y)
          .attr('rx', 2)
          .attr('ry', 2)
          .classed('connector', true)
          .on('mousedown', function (event: any, d: any) {
            // handle mousedown in a connector
            event.stopPropagation();
            // keep start of drag line
            thisEditor.mouseDownNode = {
              node: d,
              port: i,
              coords: { x:x+10+d.x, y:y+5+d.y }
            }
            thisEditor.dragLine.classed('hidden', false)
          });
      }
    });

    // draw icon area
    const icon = newNodes.append('g').classed('icon', true);
    icon.append('path').attr('d', function (d) {
      return `M 30 1 L 30 ${this.parentNode.parentNode.getBBox().height - 1}`;
    });

    // add click and double click behaviours
    let timer = null;
    newNodes.on('click', function (event:any, d:any) {

      // set node as selected
      let clickedNode = d3.select(this);
      thisEditor.resetSelection();
      clickedNode.classed('selected', true);
      thisEditor.selectedNode = clickedNode.attr('id');
      event.stopPropagation();

      // send a selected or config event to our listeners
      if (timer) {
        thisEditor.emit('configNode', d);
        clearTimeout(timer);
        timer = null;
        return;
      }
      timer = setTimeout(function () {
        thisEditor.emit('selectNode', d);
        timer = null;
      }, 200);
    });

    // add drag behaviour to the node
    newNodes.call(
      d3.drag().on('drag', function (event:any, d:any) {
          // update node location in the data and on the screen
          d.x = event.x;
          d.y = event.y;
          d3.select(this).attr('transform', `translate(${d.x}, ${d.y})`);

          // find the paths that we're connected to, and move those as well.
          for (let i in d.wires) {
            for (let node of d.wires[i])
              // outputWires.push(`wire-${d.id}-${i}-${node}`);
              thisEditor.wires.select(`#wire-${d.id}-${i}-${node}`)
                .attr('d', (d) => thisEditor.generatePath(d))
          }

          // find input wires and move those
          let inputWires = thisEditor.flowWires.filter((wire) => wire.toNode === d.id)
            .map((wire) => {
              let id = wireId(wire);
              thisEditor.wires.select(`#${id}`)
                .attr('d', (d) => thisEditor.generatePath(d))
            })
      })
    );

    // add mouseup for when you drag a link to the node
    // we currently allow you to mouseup on the node, not the connector
    newNodes.on('mouseup', function (event: any, d: any) {
      event.stopPropagation();
      // add wire to index, then update flow
      thisEditor.mouseDownNode.node.wires[thisEditor.mouseDownNode.port].push(d.id);
      // hide the drag line
      thisEditor.dragLine
        .classed('hidden',true)
        .attr('d', 'M0,0L0,0');
      thisEditor.updateFlow();
    });

    
    // remove nodes no longer there
    nodes.exit().remove();

    // now draw the links (wires)

    // in the wires layer, select all paths
    let wires = thisEditor.wires.selectAll('path');

    // generate unique ids for each wire
    wires = wires.data(this.flowWires, function (d) {
      return wireId(d);
    });

    wires.enter()
      .append('path')
      .attr('d', (d) => {
        return thisEditor.generatePath(d);
      })
      .attr('id', (d) => {
        return wireId(d);
      })
      .classed('flow-link', true)
      .on('click', function (event, d) {
        thisEditor.resetSelection();
        let link = d3.select(this);
        thisEditor.selectedLink = link.attr('id');
        link.classed('selected', true);
        event.stopPropagation();
      });
    wires.exit().remove();
  }

  // generate a node id
  private generateId() {
    return (Math.floor(1 + Math.random() * 4294967295)).toString(16);
  }

  // called when node dropped into the flow from the pallette
  addNode(type: any, x, y) {
    const wires:[string[]?] = [];
    for (let i = 0; i < this.registry.getNode(type).outputs; i++) {
      wires.push([]);
    }
    const newNode = {
      name: `new ${type} node`,
      type: type,
      id: this.generateId(),
      x: x,
      y: y,
      wires,
    };

    this.flow.push(newNode);
    this.updateFlow();
  }

  // called when configuration of node changed
  updateNode(id: string, config: any) {
    // find the node, change its config
    this.updateFlow();
  }
}
