import { html, css, LitElement } from 'lit-element';
import d3 from './d3';

import { FlowEditor, Node } from './editor';
import { NodeRegistry } from './registry';
import { Flow } from './flow';
import { styles } from './styles';

export interface NodeEvent {
  detail: Node
}

export class FlowElement extends LitElement {

  top = 0
  left = 0

  flow: Flow = null
  flowEditor: FlowEditor = null
  registry: NodeRegistry = null

  svgElement

  // For optimal performance, define scoped styles in a static styles property.
  // Define styles in a tagged template literal, using the css tag function:
  static get styles() {
    return [styles, css`
      .flow {
        position: absolute;
        margin: 0;
        top: 0px;
        left: 179px;
        bottom: 0px;
        right: 60px;
        overflow: hidden;
        border: 1px solid #bbb;
        box-sizing: border-box;
      }
      .node {
        fill: rgb(166, 187, 207);
        stroke: #999;
        cursor: move;
        stroke-width: 2;
      }
      .connector {
        fill: rgb(255,255,255);
        stroke: #999;
        stroke-width: 1;
      }
      .icon {
        stroke-opacity: .1;
        stroke: #000;
        stroke-width: 1;
      }
      .label {
        fill: #000;
        stroke: none;
        cursor: move;
        stroke-width: 1px;
        fill: black;
        user-select: none;
      }
      .hidden {
        display: none
      }
      .flow-link {
        stroke: #999;
        stroke-width: 3;
        fill: none;
        cursor: crosshair
      }
      .selected {
        stroke: #ff7f0e;
        stroke-width: 3
      }
      .flow svg:focus {
        outline: none
      }
    `]
  }

  // Uses lit-html to render the element template. You must implement render for any component
  // that extends the LitElement base class.
  render() {
    return html`
      <div class="flow">
        <svg></svg>
      </div>
    `;
  }

  // called when node dropped on to the flow editor
  dropNode(type:string, x:number, y:number) {
    this.flowEditor.addNode(type, x - this.left, y - this.top)
  }

  // called to update the specified node with an updated configuration
  updateNode(id, config) {
    this.flowEditor.updateNode(id, config)
  }

  // event - node is double clicked
  configEvent = (data: any) => {
    this.dispatchEvent(new CustomEvent('configNode', {
      detail: data
    }))
  }

  // event - node is double clicked
  selectEvent = (data: any) => {
    this.dispatchEvent(new CustomEvent('selectNode', {
      detail: data
    }))
  }

  // Called after the elements DOM has been updated the first time, immediately before updated is called.
  // Use firstUpdated for initializing private fields from DOM attributes (as the constructor doesnt have access to them).
  // Note that render has already run at this point and your changes might trigger another update lifecycle.
  firstUpdated() {
    const svgDOM = this.shadowRoot.querySelector('svg');
    this.svgElement = svgDOM;

    // get our position so we can add nodes relative to the svg port
    const rect = svgDOM.getBoundingClientRect();
    this.top = rect.top;
    this.left = rect.left;

    // svg is a d3 selection
    const svg = d3.select(svgDOM);

    svg.attr('width', '100%')
      .attr('height', '100%');

    this.flowEditor = new FlowEditor(this.registry, svg);

    // TODO: support tabs
    this.flowEditor.init(this.flow.tabs[0].nodes);

    // relay events from the editor to element listeners
    this.flowEditor.on('configNode', this.configEvent);
    this.flowEditor.on('selectNode', this.selectEvent);

    // on update, we need to call this to update flow since we can't assume it's just re-rendered
    this.flowEditor.updateFlow();
  }
}

